import { post, get, del } from '../../api/api.js';

class comment_controller_class {
  async save_comment(tweet_id,data) {
    return post(`tweets/${tweet_id}/comments`, data);
  }
  async save_reply(tweet_id,comment_id, data) {
    return post(`tweets/${tweet_id}/comments/${comment_id}`, data);
  }

  async delete_comment(tweet_id,comment_id) {
    return del(`tweets/${tweet_id}/comments/
    ${comment_id}`);
  }
}

export const comment_controller = new comment_controller_class();
