import { get } from '../../../api/api.js';
import TweetReplyView from '../../../../View/Components/Modal/post_reply/1.tweet_reply_modal_view.js';
import { comment_controller } from '../../Comment/comment_controller.js';
import { update_posted_tweet_btn_count_and_active_state } from '../../Tweet/postedTweetController.js';

const tweet_reply_el = document.querySelector('.tweet-reply');

let View;

const control_tweet_reply = async (action, tweet_id, data) => {
  if (action === 'save-comment') {
    const res = comment_controller.save_comment(tweet_id, data);
    if (!res) return;
    hide_tweet_reply_modal();

    update_posted_tweet_btn_count_and_active_state('write-comment', 1, false);
  }
};

const hide_tweet_reply_modal = () => View.hide();

export const display_tweet_reply_modal = async (tweet, cur_user) => {

  View.render(tweet, cur_user);
};

if (tweet_reply_el) {
  View = new TweetReplyView();
  View.addHandlerPostReply(control_tweet_reply);
}
