import PostedTweetView from '../../../View/Components/Tweet/posted_tweet_or_comment/posted_tweet_view.js';
import { display_tweet_reply_modal } from '../Modal/post_reply/tweet_reply_modal_controller.js';
import { post, del } from '../../api/api.js';

let View;
const postedTweetListEl = document.querySelector('.postedTweet');

export const controlRenderPostedTweet = (user, tweet) =>
  View.render(user, tweet);

export const update_posted_tweet_btn_count_and_active_state = (
  btn_type,
  count,
  active_state
) =>
  View.update_posted_tweet_btn_count_and_active_state(
    btn_type,
    count,
    active_state
  );

const like_unlike_tweet = (action, data) => {
  if (action === 'like-post')
    post(`users/like-tweets/add/tweet/${data.post_id}`);
  if (action === 'unlike-post')
    del(`users/like-tweets/remove/tweet/${data.post_id}`);
};
const bookmark_unbookmark_tweet = (action, data) => {
  if (action === 'bookmark-post')
    post(`users/bookmark-tweets/add/tweet/${data.post_id}`);
  if (action === 'unbookmark-post')
    del(`users/bookmark-tweets/remove/tweet/${data.post_id}`);
};

const retweet_quote_tweet = (action, data) => {
  const { tweet_id } = data;
  if (action === 'retweet') post(`tweets/like/${tweet_id}`);
  if (action === 'delete-retweet') del(`tweets/${tweet_id}`);
  if (action === 'quote-tweet') post(`tweets/quote-tweet/${tweet_id}`);
};

const reply_to_tweet = (action, data) => {
  // if(action==="tweet-reply")
};

const redirect_to_user = (user_avatar) =>
  location.assign(`/users/${user_avatar.slice(1)}`);

const redirect_to_tweet_comments = (tweet_id) =>
  location.assign(`/tweets/${tweet_id}}`);

const controlPostedTweets = (action, data) => {
  console.log(action, data);
  like_unlike_tweet(action, data);
  bookmark_unbookmark_tweet(action, data);
  if (action === 'redirect-to-user') redirect_to_user(data.avatar);
  if (action === 'redirect-to-post') redirect_to_tweet_comments(data.post_id);

  // tweet-id
  if (action === 'display-reply-modal')
    display_tweet_reply_modal(data.post, data.cur_user);
};

if (postedTweetListEl) {
  View = new PostedTweetView();
  View.addHandlerPostedTweet(controlPostedTweets);
}
