import ParentCreateTweetView from './0.ParentCreateTweetView.js';
import AlertView from '../../Alert/AlertView.js';
import { replaceClass } from '../../../utils/domHelper.js';
import DiscardTweetPopupView from '../../Popup/MultipleTweet/DiscardMultiTweetPopupView.js';
import SaveMultiTweetPopupView from '../../Popup/MultipleTweet/SaveMultiTweetPopupView.js';

class CreateMultipleTweetsView extends ParentCreateTweetView {
  _topParentEl = document.querySelector('.createTweets');
  _topParentContainerEl = this._topParentEl.querySelector(
    '.createTweets-container'
  );
  _tweetListEl = this._topParentEl.querySelector('.createTweets-list');
  _overlay = this._topParentEl.querySelector('.overlay');
  _saveAllTweetsBtn;

  // CLASS __________________________________
  _DiscardTweetPopupView;
  _SaveMultiTweetPopupView;

  constructor() {
    super();
    this._hideParentWhenOverlayIsClicked();
  }

  setSomeOtherComponentClass() {
    this._DiscardTweetPopupView = new DiscardTweetPopupView();
    this._SaveMultiTweetPopupView = new SaveMultiTweetPopupView();
  }

  setMoreDOMEls() {
    this._saveAllTweetsBtn = this._parentEl.querySelector(
      'button[data-action="save-all-tweets"]'
    );
  }

  _hide() {
    this._topParentEl.classList.replace('view', 'hidden');
  }
  _show() {
    this._topParentEl.classList.replace('hidden', 'view');
  }

  _getAllTweetItems() {
    return [...this._topParentEl.querySelectorAll('.createTweets-item')];
  }

  _getTotalCountOfTweetItemInUse() {
    return this._getAllTweetItems().filter(
      (el) => el.dataset.tweetInUse === 'true'
    ).length;
  }

  _getCurrentActiveTweetItem() {
    return this._topParentEl.querySelector(
      '.createTweets-item[data-active="true"]'
    );
  }

  _setDatasetObjInEl(tweet_item, dataset_obj) {
    Object.keys(dataset_obj).forEach((key) => {
      tweet_item.dataset[key] = dataset_obj[key];
    });
  }

  _setTweetItemActive(cur_tweet_item) {
    const dataset = {
      active: true,
      tweetMini: false,
      tweetInUse: true,
      // hideTweetTrackLine: false,
    };
    this._setDOMEls(cur_tweet_item);

    this._setDatasetObjInEl(cur_tweet_item, dataset);

    this._getAllTweetItems().forEach((el) => {
      if (el !== cur_tweet_item) {
        el.dataset.active = false;
        el.dataset.tweetMini = true;
        el.dataset.hideTweetTrackLine = false;
      }
    });

    //////////////////////////////////////////////////////////////////////////////////
    // hide last active tweet item track line
    const lastTweetItemInUseEl = [
      ...this._topParentContainerEl.querySelectorAll(
        '.createTweets-item[data-tweet-in-use="true"]'
      ),
    ].slice(-1)[0];

    cur_tweet_item.dataset.hideTweetTrackLine =
      lastTweetItemInUseEl === cur_tweet_item;

    cur_tweet_item.classList.remove('hidden');
  }

  _handleSelectTweetItemEl(target, optionalItem = false) {
    if (target.closest('button[data-action="add-another-tweet"]')) return;
    const tweet_item = optionalItem
      ? optionalItem
      : target.closest('.createTweets-item');
    if (!tweet_item && !optionalItem) return;

    this._setTweetItemActive(tweet_item);
  }

  _switchBwTweetBtnAndTweetAllBtn() {
    this._tweetListEl.dataset.multipleTweets =
      this._getTotalCountOfTweetItemInUse() > 1 ? 'true' : 'false';

    this._tweetListEl.dataset.multipleTweets
      ? this._saveAllTweetsBtn.removeAttribute('disabled')
      : this._saveAllTweetsBtn.setAttribute('disabled');
  }

  _hideParentWhenOverlayIsClicked() {
    this._overlay.addEventListener('click', () => {
      return this._DiscardTweetPopupView.show();

      this._hide();
      // this.emptyAllInputsValue();
    });
  }

  _handleTweetItemRemoveBtn(target) {
    const removeTweetItemBtn = target.closest(
      'button[data-action="remove-tweet"]'
    );
    if (!removeTweetItemBtn) return;

    // remove tweet
    const removeTweetItem = removeTweetItemBtn.closest('.createTweets-item');
    removeTweetItem.classList.add('hidden');
    const dataset = {
      active: false,
      tweetSchedule: false,
      tweetEmpty: true,
      tweetInUse: false,
      mini: true,
    };
    this._setDatasetObjInEl(removeTweetItem, dataset);

    // find the tweet before the remove tweet and make it active
    let { tweetNum } = removeTweetItem.dataset;

    let findTweetItem = this._getAllTweetItems().find(
      (el) => Number(el.dataset.tweetNum) === Number(tweetNum) - 1
    );
    if (!findTweetItem)
      findTweetItem = this._getAllTweetItems().find(
        (el) => Number(el.dataset.tweetNum) === Number(tweetNum) + 1
      );

    if (!findTweetItem) this._hide();

    this._setTweetItemActive(findTweetItem);

    // if tweet is one left then set list multiple-tweets attribute to false
    if (this._getTotalCountOfTweetItemInUse() === 1) {
      this._tweetListEl.dataset.multipleTweets = 'false';

      findTweetItem.querySelector(
        'button[data-action="choose-audience"]'
      ).style.display = 'block';
    }
  }

  ////////////////////////////////////////////////////
  // -  TWEET ITEM ADDITIONAL FUNCTION
  ////////////////////////////////////////////////////
  handleAddMultipleTweetsBtn(target) {
    const btn = target.closest("button[data-action='add-another-tweet']");
    if (!btn) return;

    // we can do this by displaying the left tweet

    // lets get the last item
    const nextTweetItem = this._getAllTweetItems().find(
      (el) => el.dataset.tweetInUse === 'false'
    );

    if (!nextTweetItem) return new AlertView().render('Max Tweet Limit 5');

    this._handleSelectTweetItemEl(nextTweetItem);
  }

  addAdditionalCodeToShowUploadImgFunc() {
    this._makeHeaderStickyWhenHeightOverflowContainer();
  }

  addAdditionalCodeToHandleChooseWhoCanReplyBtn() {
    // make choose option value in all btns same when choosen by any reply btn
    // if reply btn is disabled then all should be also

    const btn_value = this._chooseWhoCanReplyBtn.querySelector(
      '.preview-parent-text'
    ).textContent;
    const isBtnDisabled = this._chooseWhoCanReplyBtn.disabled;
    const chooseWhoCanReplyBtnsValueArr = [
      ...this._topParentEl.querySelectorAll(
        'button[data-action="choose-whocan-reply"] .preview-parent-text '
      ),
    ];
    chooseWhoCanReplyBtnsValueArr.forEach((el) => {
      el.textContent = btn_value;
      el.closest('button').querySelector('input').value = btn_value;
      if (isBtnDisabled)
        el.closest('button[data-action="choose-whocan-reply"]').disabled = true;
    });
  }

  addAdditionalCodeToHandleChooseAudienceBtn() {
    const btn_value = this._chooseAudienceBtn.querySelector(
      '.preview-parent-text'
    ).textContent;

    const choooseAudienceAllBtns = [
      ...this._topParentEl.querySelectorAll(
        'button[data-action="choose-audience"] input'
      ),
    ];
    choooseAudienceAllBtns.forEach((el) => (el.value = btn_value));

    const chooseWhoCanReplyBtnsValue = [
      ...this._topParentEl.querySelectorAll(
        'button[data-action="choose-whocan-reply"] .preview-parent-text'
      ),
    ];

    if (btn_value === 'everyone') {
      chooseWhoCanReplyBtnsValue.forEach((el) => {
        el.closest('button').querySelector('input').value = btn_value;

        el.textContent = 'Everyone can reply';
        el.closest(
          'button[data-action="choose-whocan-reply"] '
        ).disabled = false;
      });
    } else {
      chooseWhoCanReplyBtnsValue.forEach((el) => {
        el.closest('button').querySelector('input').value = btn_value;

        el.textContent = 'Only your Twitter Circle can reply';
        el.closest(
          'button[data-action="choose-whocan-reply"] '
        ).disabled = true;
      });
    }
  }

  // scrollHeight
  _makeHeaderStickyWhenHeightOverflowContainer(setTrue = false) {
    // scrollHeight increase when content of container is overflowing

    if (
      this._topParentContainerEl.scrollHeight >
        this._topParentContainerEl.clientHeight ||
      setTrue
    )
      this._topParentContainerEl.dataset.stickyHeader = 'true';
  }

  _handleUnsentTweetBtn(target) {
    const btn = target.closest('button[data-action="show-unsent-tweets"]');
    // console.log(btn);
    if (!btn) return;

    // if tweet in use === 1 then if clicked on unsent tweet allow to save tweet
    if (
      this._getTotalCountOfTweetItemInUse() === 1 &&
      this._textAreaEl.value &&
      this._textAreaEl.value !== ''
    ) {
      return this._SaveMultiTweetPopupView.show();
    }

    const unsentTweetModalEl = document.querySelector(
      '.modal[data-modal="unsent-tweets"]'
    );
    replaceClass(unsentTweetModalEl, 'hidden', 'display');

    // const View = new UnsentTweetModalView();
    // View.show();
    // View.handleModal();
  }

  // FUNCTION WORKING ON TWEET ITEM
  additionalHandleFuncs(target, handle) {
    // CSS ------------------------------------------------------------
    this._makeHeaderStickyWhenHeightOverflowContainer();
    // active item on clicking (unhide the hidden item els)
    this._handleSelectTweetItemEl(target);

    // change tweet btn to tweet all when no of tweets is more than one on active tweet item
    this._switchBwTweetBtnAndTweetAllBtn();

    this._handleTweetItemRemoveBtn(target);

    // HANDLE -----------------------------------------------
    this._handleSaveMultipleTweets(target, handle);
  }

  // FUNCTION ON TWEET TOP PARENT E
  handleTweetTopParentEl(target) {
    this._handleUnsentTweetBtn(target);
  }

  // add one more empty tweet in tweet  list
  addHandlerMultipleTweets(handle) {
    this.setUsedComponentClass();

    this.handleTweet(handle);
    this._makeHeaderStickyWhenHeightOverflowContainer();
  }

  // ------------------------------------------------------
  // To set Changes in multiple tweets from controller
  _setItemActiveAndItsDOMElsForAccessAndReturnEl(firstTweet) {
    const activeTweetItemEl = firstTweet
      ? this._topParentEl.querySelector('.createTweets-item')
      : this._topParentEl.querySelector(
          '.createTweets-item[data-active="true"]'
        );

    // to access it dom elements of createTweet item
    this._setDOMEls(activeTweetItemEl);

    // to show tweet item as active
    this._handleSelectTweetItemEl(activeTweetItemEl);

    return activeTweetItemEl;
  }

  // if first tweet item true means set image in first tweet item
  setUploadImgInMultipleTweetImgEls(firstTweet, imgArr) {
    this._setItemActiveAndItsDOMElsForAccessAndReturnEl(firstTweet);

    // if arr is img not files then it means it is from unsent Tweets ,if  arr is of img files then arr is from single create tweet
    imgArr
      ? this._setUploadImgsAndshow('img', imgArr)
      : this._setUploadImgsAndshow('files');

    if (firstTweet) {
      // now set the second tweet if the first tweet  upload img is set by single tweet
      this._handleSelectTweetItemEl(
        this._topParentEl.querySelector(
          '.createTweets-item[data-tweet-new="true"]'
        )
      );

      this._makeHeaderStickyWhenHeightOverflowContainer(true);
    }
  }

  setUnsentTweetInMultiplTweetForm(tweetObj) {
    const activeTweetEl =
      this._setItemActiveAndItsDOMElsForAccessAndReturnEl(true);

    // select first item to to access the textarea el thorught setDOMELS
    let { type, text, id, schedule_date, imgArr } = tweetObj;

    activeTweetEl.dataset.tweetId = id;
    // setting the tweet

    this._textAreaEl.value = text ? text : '';
    this._textAreaEl.textContent = text ? text : '';

    imgArr !== '' ? this.setUploadImgInMultipleTweetImgEls(true, imgArr) : '';

    // if (type === 'scheduled') {
    //   activeTweetEl.dataset.tweetSchedule = true;
    //   this._scheduleTimingSpanValueEl.textContent = schedule_date;
    // }
  }

  // get only one left tweet item input value to save as draft tweet
  getLastLeftTweetData() {
    // return current click tweet item el
    const formdata = this.getTweetDataAsFormData();

    formdata.set('tweet_type', 'draft');
    return formdata;
  }

  // SUBMIT

  _handleSaveMultipleTweets(target, handle) {
    if (!target.closest('button[data-action="save-all-tweets"]')) return;

    const tweets_data_obj = {};

    // 1.get all tweet in use
    const tweetInUse = [
      ...this._topParentEl.querySelectorAll(
        '.createTweets-item[data-tweet-in-use="true"]'
      ),
    ];

    // looping through to set related tweet properties
    tweetInUse.forEach((tweet, i) => {
      const tweet_data = this._getAllInputsValue(tweet);

      // check for draft tweet 
      if(tweet.dataset.tweetId) tweet_data.tweet_id=tweet.dataset.tweetId;


      console.log('dat', tweet_data);

      if (!tweet_data.text && [...tweet_data.upload_imgs].length === 0) return;

      // filter empty tweet

      // slicing which user after left after editing workaround
      const no_of_upload_img =
        tweet.querySelector('.grid--collage').dataset.items;

      if (tweet_data.upload_imgs) {
        tweet_data.upload_imgs = [...tweet_data.upload_imgs].slice(
          0,
          Number(no_of_upload_img)
        );
      }

        

      tweet_data.tweet_type = tweet_data.schedule_post_time
        ? 'schedule'
        : 'post';

      // to let server know this tweet is from multiple tweets
      tweet_data.multiple_tweets = true;

      // creating formdata
      const tweet_form_data = new FormData();
      Object.keys(tweet_data).forEach((key) => {
        if (tweet_data[key] === null || tweet_data[key] === '') return;

        if (key === 'upload_imgs') return;
        tweet_form_data.append(key, tweet_data[key]);
      });

      // taking care of upload_imgs
      if (tweet_data.upload_imgs)
        tweet_data.upload_imgs.forEach((img) =>
          tweet_form_data.append('upload_imgs', img)
        );

      tweets_data_obj[i] = tweet_form_data;
    });

    console.log(tweets_data_obj);

    handle('save-all-tweets', tweets_data_obj);
  }
}
export default CreateMultipleTweetsView;
