import create_comment_view from '../../Tweet/createTweet/3.create_comment_view.js';
import DiscardTweetPopupView from '../../Popup/MultipleTweet/DiscardMultiTweetPopupView.js';
import SaveMultiTweetPopupView from '../../Popup/MultipleTweet/SaveMultiTweetPopupView.js';

// comment model on the home page to just add a comment without going to comments
// to see more comments and reply to other comment we use commentController and redirected to new page

class PostReplyModal extends create_comment_view {
  _topParentEl = document.querySelector('.tweet-reply');
  _overlay = this._topParentEl.querySelector('.overlay');

  _post_reply_content_el = this._topParentEl.querySelector(
    '.tweet-reply-content'
  );

  hide() {
    this._topParentEl.classList.replace('view', 'hide');
  }

  show() {
    this._topParentEl.classList.replace('hide', 'view');
  }

  //   post => tweet or comment
  render(post, cur_user_data) {

    const tweet_user = post.post_user;
    const tweet =post.post_data;
    this._topParentEl.dataset.tweetId = tweet._id;
    this._topParentEl.dataset.userAvatar = tweet_user.avatar;
    this._topParentEl.dataset.tweetUserId = tweet_user._id;

    this._post_reply_content_el.innerHTML = '';

    const html = `   
    <img class="tweet-img img--md" src="/img/users/${tweet_user.profilePic}" />
    <div class="tweet-content tweet-reply-detail">
        <div class="tweet-user__info preview-item" data-no-hover-change="">
          <p class="tweet-user__name h-8 hover-underline">${tweet_user.name} </p>
          <p class="tweet-user__verified"></p>
          <p class="tweet-user__email t--md">${tweet_user.avatar}</p>
          <p class="tweet-user__postTime t--md hover-underline">${tweet.ts_format}</p>
    </div>
    <p class="p--md mg-sm">${tweet.text}</p>
    <div class="tweet-message mg-lw t--md">Replying to
       <span class="blue"> ${tweet_user.avatar} </span>
    </div>
    </div>
    `;

    this._topParentEl.querySelector(
      '.createTweet-user__img'
    ).src = `/img/users/${cur_user_data.profilePic}`;

    this._post_reply_content_el.insertAdjacentHTML('afterbegin', html);

    this.show();
  }

  setSomeOtherComponentClass() {
    this._DiscardTweetPopupView = new DiscardTweetPopupView();
    this._SaveMultiTweetPopupView = new SaveMultiTweetPopupView();
  }

  _hideParentWhenOverlayIsClicked() {
    this._overlay.addEventListener('click', () => {
      return this._DiscardTweetPopupView.show();

      this._hide();
      // this.emptyAllInputsValue();
    });
  }

  _handleUnsentTweetBtn(target) {
    const btn = target.closest('button[data-action="show-unsent-tweets"]');
    // console.log(btn);
    if (!btn) return;

    if (this._topParentEl.querySelector('textarea').value === '') return;

    // if tweet in use === 1 then if clicked on unsent tweet allow to save tweet

    return this._SaveMultiTweetPopupView.show();

    const unsentTweetModalEl = document.querySelector(
      '.modal[data-modal="unsent-tweets"]'
    );
    replaceClass(unsentTweetModalEl, 'hidden', 'display');

    // const View = new UnsentTweetModalView();
    // View.show();
    // View.handleModal();
  }

  handleTweetTopParentEl(target) {
    this._handleUnsentTweetBtn(target);
  }


  addHandlerPostReply(handle) {
    this._hideParentWhenOverlayIsClicked();

    this.setUsedComponentClass();
    this.handleTweet(handle);
  }
}
export default PostReplyModal;
