import ParentCreateTweetView from './0.ParentCreateTweetView.js';
import { addClass } from '../../../utils/domHelper.js';

class create_comment_view extends ParentCreateTweetView {
  _topParentEl = document.querySelector('.tweet-reply');
  emptyAllInputsValue() {
    // parent el
    this._parentEl.setAttribute('data-active', false);
    this._parentEl.setAttribute('data-tweet-schedule', false);
    this._parentEl.setAttribute('data-tweet-empty', true);

    // schedule timing label
    addClass(this._scheduleTimingEl, 'hidden');
    this._scheduleTimingSpanValueEl.value = '';

    this._textAreaEl.value = '';

    this._uploadImgsMainContainerEl.classList.add('hidden');
    this._uploadImgElsArr.forEach((el) => (el.src = ''));

    // percent complete circle
    this._percentageCircleControllerEl.setAttribute(
      'stroke-dasharray',
      `0,100`
    );

    // input values
    [...this._parentEl.querySelectorAll('input')].forEach(
      (el) => (el.value = '')
    );
    this._uploadImgInputEl.value = '';
    this._uploadImgInputEl.files = undefined;
    // img
  }

  // Submit data to db(single tweet)
  _handleSaveTweet(target, handle) {
    const tweetBtn = target.closest('button[data-action="save-tweet"]');
    if (!tweetBtn) return;

    const comment = this._getAllInputsValue();

    comment.tweet_avatar = this._topParentEl.dataset.userAvatar;
    comment.tweet_id = this._topParentEl.dataset.tweetId;
    comment.tweet_user_id = this._topParentEl.dataset.tweetUserId;

    const form_data = new FormData();
    Object.keys(comment).forEach((key) => {
      if (!comment[key] || comment[key] === '' || key === 'upload_imgs') return;
      form_data.append(key, comment[key]);
    });

    if (comment.upload_imgs && comment.upload_imgs.length > 0) {
      [...comment.upload_imgs].forEach((file) =>
        form_data.append('upload_imgs', file)
      );
    }

    handle('save-comment', this._topParentEl.dataset.tweetId, form_data);

    // reset inputs
    this.emptyAllInputsValue();
  }
}

export default create_comment_view;
