import CreateMultipleTweetsView from '../../../View/Components/Tweet/createTweet/2.CreateMultipleTweetsView.js';
import { post } from '../../api/api.js';
const createMultipleTweetsEl = document.querySelector('.createTweets');

let View;

export const controlSetUploadImgInMultipleTweetImgEls = () => {
  View.setUploadImgInMultipleTweetImgEls(true);
};

export const controlSetUnsentTweetInMultiplTweetForm = (tweetObj) => {
  View._show();
  View.setUnsentTweetInMultiplTweetForm(tweetObj);
};

export const getCreateMultipleTweetsView = () => View;

const controlSaveSingleTweet = async (data) => {
  const res = await post('tweets', data);
  console.log(res);
  if (!res) return;

  location.assign('/');
};

const controlSaveMultipleTweets = async (data) => {
  try {
    const tweets_save = [];

    for (const key of Object.keys(data)) {
      const tweet_formdata = data[key];

      const save_tweet = post('tweets', tweet_formdata);
      tweets_save.push(save_tweet);
    }

    await Promise.all(tweets_save);
  } catch (err) {
    console.error('multiple tweets not saved');
    return;
  }

  location.reload();
};

export const controlSaveTweetAsDraft = async () => {
  // return formData
  const tweet_data = View.getLastLeftTweetData();
  return await post('tweets', tweet_data);
};

const controlMultipleTweets = async (action, data) => {
  if (action === 'save-tweet') controlSaveSingleTweet(data);
  if (action === 'save-all-tweets') controlSaveMultipleTweets(data);
};

if (createMultipleTweetsEl) {
  View = new CreateMultipleTweetsView();
  View.addHandlerMultipleTweets(controlMultipleTweets);
}
