import ParentPreview from '../../Common/Preview.js';
class ChooseAudiencePreview extends ParentPreview {
  _parentElSelector = '.preview[data-type="choose-audience"]';

  handleAdditionalFunc(handle,target) {
    // handle edit circle btn
    const el = target.closest('[data-action="open-user-edit-circle"]');

    if (!el) return;

    const edit_circle_modal = document.querySelector(
      '.modal[data-action="edit-your-circle"]'
    );

    
    
    const show_circle = handle('show-user-cicle')
    if(show_circle)
    edit_circle_modal.classList.replace('hidden', 'display');

  }


  addHandlerChooseAudience(handle){

    document.addEventListener('click', (e) => {

      const target = e.target;

      this._parentEl = e.target.closest('.preview[data-type="choose-audience"]')
      if(!this._parentEl) return



      


      this._set_item_active(target);

      this._hideOtherPreviewExceptCurOne();

      this._setDOMEls();

      this.handleAdditionalFunc(handle, target);

      // //  open new component from preview
      // if (target.closest('.preview-open-component')) {
      //   const { action } = target.closest('.preview-open-component').dataset;
      //   this._hide();
      //   return handle(action);
      // }

      // Select item and set value;
      const preview_item = target.closest('.preview-item');
      if (!preview_item) return;

      const { value } = preview_item.dataset;
      this._parentTextEl.textContent = value;
      this._parentInputEl.value = value;

      this._hide();
    });
  }
}

export default ChooseAudiencePreview;
