  _generate_markUp(tweet) {
    const upload_imgs = tweet.upload_imgs;
    const total_upload_imgs = tweet.upload_imgs.length;
    const upload_imgs_exist = total_upload_imgs > 0 ? true : false;

    const user = tweet.user_details;

    return `
        <div class="tweet preview-item-container postedTweet" data-user-id=${
          tweet.user_id
        } data-tweet-id=${tweet._id} data-user-name=${user.name}>
          <div class="tweet-container" data-active="false">
            <img class="tweet-img img--md" src=${
              user.profilePic
            } onerror="this.src='/img/users/default.png'"/>
            <div class="tweet-tag subheading hover-underline hide">${
              user.category
            }</div>
            <div class="tweet-content">
              <div class="tweet-user__info preview-item" data-hover="" data-no-hover-change="" data-name=${
                user.name
              } data-avatar=${user.avatar} data-description=${
      user.bio
    } data-following_count=${user.following_count} data-followers_count=${
      user.followers_count
    }>
                <p class="tweet-user__name h-8 hover-underline" data-preview-window-target-el="true">${
                  user.name
                }</p>
                <p class="tweet-user__verified" data-preview-window-target-el="true"></p>
                <p class="tweet-user__email t--md" data-preview-window-target-el="true">${
                  user.avatar
                }</p>
                <p class="tweet-user__postTime t--md hover-underline">${
                  tweet.ts_format
                }</p>
              </div>
              <button class="tweet-btn tweet-options-btn btn--icon-dim-primary" data-dropdown-btn="data-dropdown-btn" data-dropdown-class="tweet-options-dropdown" data-active="false">
                <div class="icon-box"><i class="fas fa-ellipsis"></i></div>
              </button>
              <div class="tweet-message mg-lw p--md">${tweet.text}</div>
              <div class="tweet-uploadImg__container mg-lw ${
                upload_imgs_exist ? '' : 'hidden'
              } ">
              <div class="tweet-uploadImg__content grid--collage" data-items=${total_upload_imgs}  >
  
              ${[0, 1, 2, 3]
                .map(
                  (count) => `
                <figure class="tweet-uploadImg__fig ${
                  upload_imgs[count] ? '' : 'hidden'
                }">
                    <img class="tweet-uploadImg" src=${
                      upload_imgs[count]
                        ? `/img/tweet/${upload_imgs[count]}`
                        : ''
                    }  alt=""/>
                </figure>
                    `
                )
                .join('')}
                </div>
              </div>
              <div class="tweet-btns">
                <button class="tweet-btn btn--icon-dim-primary" data-action="write-comment">
                  <div class="icon-box"><i class="fa fa-commenting-o"></i></div><span class="tweet-btn__value btn--icon-value"></span>
                </button>
                <button class="tweet-btn btn--icon-dim-secondary" data-action="retweet">
                  <div class="icon-box"><i class="fa fa-retweet"></i></div><span class="tweet-btn__value btn--icon-value"></span>
                </button>
                <button class="tweet-btn btn--icon-dim-tertiary" data-action="like">
                  <div class="icon-box"><i class="fa fa-heart-o"></i></div><span class="tweet-btn__value btn--icon-dim-value"></span>
                </button>
                <button class="tweet-btn btn--icon-dim-primary" data-action="bookmark">
                  <div class="icon-box"><i class="fa fa-bookmark-o"></i></div>
                </button>
              </div>
            </div>
          </div>
          <div class="line line-dim">&nbsp;</div>
        </div>`;
  }
