import UnsentTweetModalView from '../../../View/Components/Modal/UnsentTweetModalView.js';
import { del } from '../../api/api.js';
import { show_delete_draft_tweet_popup } from '../Popup/draftTweetPopupController.js';
import {
  controlSetUnsentTweetInMultiplTweetForm,
  getCreateMultipleTweetsView,
} from '../Tweet/createMutltipleTweetsController.js';

const UnsentDraftTweetModalEl = document.querySelector(
  ".modal[data-modal='unsent-tweets']"
);

let View;
let to_be_delete_draft_tweets = {};

// insert draft tweet into multiple tweet form
export const controlInsertTweetIntoCreateTweetsModal = (tweetObj) => {
  console.log(tweetObj)
  controlSetUnsentTweetInMultiplTweetForm(tweetObj);
  View.hide();
};

export const getDraftTweetModalView = () => View;



export const controlDeleteUnsentTweets = async (data) => {
  if (!to_be_delete_draft_tweets.tweetIds) {
    to_be_delete_draft_tweets = data;

    show_delete_draft_tweet_popup();
    return;
  }

  const { type, tweetIds } = to_be_delete_draft_tweets;

  const to_be_delete_tweets_arr = [];
  for (const tweet_id of tweetIds)
    to_be_delete_tweets_arr.push(
      del(`users/${type}-tweets/remove/tweet/${tweet_id}`)
    );
  await Promise.all(to_be_delete_tweets_arr);

  location.reload();
};

const go_to_home = () => location.assign('/');

const go_to_section_page = (section) =>
  location.assign(`/tweets/unsent/${section}`);

export const controlSaveTweetAsDraft = () => {};

export const controlDraftTweetModal = (action, data) => {
  console.log(action);

  if (action === 'insert-tweet-in-multiple-tweet-modal')
    controlInsertTweetIntoCreateTweetsModal(data);

  if (action === 'delete-tweets') controlDeleteUnsentTweets(data);

  if (action === 'save-tweet') controlSaveTweetAsDraft(data);

  if (action === 'redirect-to-home') go_to_home();

  if (action === 'redirect-to-section') go_to_section_page(data.section);
};

if (UnsentDraftTweetModalEl) {
  View = new UnsentTweetModalView();
  View.addHandlerModal(controlDraftTweetModal);
}
