import PopupView from '../../../Common/PopupView.js';
import { controlSaveTweetAsDraft } from '../../../../Controller/Components/Modal/unsendTweetsController.js';
// present in multi tweets
class DiscardDraftTweetPopupView extends PopupView {
  _parentEl = document.querySelector('.popup[data-tweet-type="draft-tweets"]');

  handleDeleteAllTweetBtn(target, handle) {
    const btn = target.closest("button[data-action='delete-tweets']");
    if (!btn) return;
    handle('delete-draft');
    this.hide();
  }
}

export default DiscardDraftTweetPopupView;
