// whenever a component is used twice we should make the component as common View for that component so another component just like can use the component
import { replaceClass } from '../../../utils/domHelper.js';

import ParentCreateTweetView from './0.ParentCreateTweetView.js';

class CreateTweetView extends ParentCreateTweetView {
  _topParentEl = document.querySelector('.createTweet-main');
  // _parentEl = document.querySelector(
  //   '.form--createTweet[data-multiple-tweets="false"]'
  // );

  // FORM -------------------------------------------------------
  _multipleTweetForm = document.querySelector('.createTweets');

  // INPUT ----------------------------------------------------------

  // so methods to be called automatically
  constructor() {
    // this refers to current class CreateTweetView
    super();
  }

  // setUsedComponentClass() {
  //   this._ScheduleView = new ScheduleTweetFormView();
  // }

  _passTweetContentToMultipleTweetForm(handle) {
    const cur_tweet_content = this._textAreaEl.value;
    const inputsValue = this._getAllInputsValue();
    console.log(inputsValue);

    const multipleTweetFormFirstItem =
      this._multipleTweetForm.querySelector('.createTweets-item');
    const multiple_tweet_textarea_input =
      multipleTweetFormFirstItem.querySelector('.createTweet-textarea');

    // audience btn
    const multipleTweetAudienceBtn = multipleTweetFormFirstItem.querySelector(
      'button[data-action="choose-audience"]'
    );
    const multipleTweetAudienceTextEl =
      multipleTweetAudienceBtn.querySelector('span');
    const multipleTweetAudienceInputEl =
      multipleTweetAudienceBtn.querySelector('input');

    // audience btn
    const multipleTweetAudienceCanReplyBtn =
      multipleTweetFormFirstItem.querySelector(
        'button[data-action="choose-whocan-reply"]'
      );
    const multipleTweetAudienceCanReplyTextEl =
      multipleTweetAudienceCanReplyBtn.querySelector('span');
    const multipleTweetAudienceCanReplyInputEl =
      multipleTweetAudienceCanReplyBtn.querySelector('input');

    const multipleTweetUploadImgInput =
      multipleTweetFormFirstItem.querySelector('[name="upload_image"]');
    /////////////////////////////////////////////////////////////////////////////

    if (inputsValue.upload_imgs.length > 0) {
      console.log('true');
      multipleTweetUploadImgInput.files = inputsValue.upload_imgs;
      handle('show-upload-img-in-multiple-tweet-form');
    }

    // target audience
    multipleTweetAudienceTextEl.textContent = inputsValue.target_audience;
    multipleTweetAudienceInputEl.textContent = inputsValue.target_audience;

    // allegieblle to reply
    multipleTweetAudienceCanReplyTextEl.textContent =
      inputsValue.allegible_to_reply;
    multipleTweetAudienceCanReplyInputEl.textContent =
      inputsValue.allegible_to_reply;

    if (cur_tweet_content)
      multiple_tweet_textarea_input.value = cur_tweet_content;
  }

  // FUNCTION BELONG TO PARENT VIEW
  handleAddMultipleTweetsBtn(target, handle) {
    const btn = target.closest("button[data-action='add-another-tweet']");
    if (!btn) return;
    this._passTweetContentToMultipleTweetForm(handle);
    replaceClass(this._createMutipleTweetsContainer, 'hidden', 'view');
    this.emptyAllInputsValue(true);
  }

  addHandlerTweet(handle) {
    this.setUsedComponentClass();

    this.handleTweet(handle);
  }
}

export default CreateTweetView;
